@charset "UTF-8";
/* #region Глобальные стили*/
:root {
  --min-width: 280px; /* Минимальный размер до которого сжимается сайт без маштабирования*/
  --font-color-basic: #000000; /*Основной цвет текста документа*/
  --font-color-inv: #ffffff; /* Комплементарный цвет текста документа*/
  --font-color-secondary: #00659e; /* Цвет текста дополнительный  */
  --bg-color-basic: #ffffff; /* Основной цвет фона документа */
  --bg-color-secondary: #00659e; /* Основной цвет фона документа */
  --bg-color-modal: #ffb820; /* Цвет фона для модального окна */
  --font-color-additional: #303030; /* Вспомогательный цвет текста некоторых секций */
  --header-color-secondary: #544f4f; /* Вспомогательный цвет текста для расписания рабочего времени и адреса в "шапке" сайта  --header-bg-color: #ffffff;           // Фон "шапки" */
  --main-color-secondary: #544f4f; /* Вспомогательный цвет текста для описания примеров */
  --main-bg-color-additional: #f5f5f5; /* Дополнительный фон у некоторых разделов главного содержимого */
  --footer-color-seconadary: #032233; /* Вспомогательный цвет текста в "подвале" */
  --footer-bg-color: #f5f5f5; /* Фон "подвала" */
  --btn-bg-basic: #00659e; /* Фон кнопок основной */
  --btn-bg-secondary: #ffb820; /* Фон кнопок дополнительный */
  --form-text-size: 16px; /* Размера шрифта в формах */
  --form-line-height: 18px; /* Высота строки текста в формах */
  --form-input-border: #acacac; /* Цвет рамки для поля ввода в формах */
  --accordion-underline-color: #acacac; /* Цвет нижней линнии позиции аккордиона */
}

@font-face {
  font-family: "Roboto";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/roboto.woff2") format("woff2"), url("../fonts/roboto.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/robotobold.woff2") format("woff2"), url("../fonts/robotobold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto cond";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/robotocondensed.woff2") format("woff2"), url("../fonts/robotocondensed.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto cond";
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/robotocondensedbold.woff2") format("woff2"), url("../fonts/robotocondensedbold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

*[class*=title],
*[class*=heading],
*[class*=text] {
  margin: 0;
}

*[class*=link],
*[class*=button] {
  color: inherit;
  text-decoration: none;
}

*[class*=input] {
  font: inherit;
  color: inherit;
}

*[class*=list],
*[class*=menu] {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

/* #endregion */
/* #region Стили стандартных элементов*/
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  color: inherit;
  text-transform: uppercase;
  box-shadow: 0px 5px 15px rgba(79, 64, 0, 0.15), inset 0px -1px 0px rgba(23, 22, 22, 0.2);
  border: none;
  border-radius: 50px;
  cursor: pointer;
}
.button--primary {
  color: var(--font-color-inv);
  background-color: var(--btn-bg-basic);
}
.button--secondary {
  color: var(--font-color-basic);
  background-color: var(--btn-bg-secondary);
}
.button--details {
  color: var(--font-color-secondary);
}
.button--primary:hover {
  background-color: #007CC1;
}
.button--secondary:hover {
  background-color: #FFCE65;
}

.address {
  font-style: normal;
}

.list {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1190px) {
  .list {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.list__item {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1190px) {
  .list__item {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.list__link {
  text-decoration: underline;
}

.sub-list {
  padding-left: 2rem;
  list-style-image: url("../img/icon_slider_true.svg");
}

.sub-list__item {
  padding-left: 1%;
}
.sub-list__item:not(:last-of-type) {
  margin-bottom: 10px;
}

.images {
  width: 100%;
  height: auto;
  object-fit: cover;
}
.images--over {
  height: 100%;
}

.form {
  display: flex;
  flex-direction: column;
}

.form__group {
  display: flex;
  flex-direction: column;
}

.form__input {
  width: 100%;
  font-size: 16px;
  line-height: 16px;
  padding: 16px 12px;
  margin-bottom: 20px;
  color: var(--font-color-basic);
  border: 1px solid var(--form-input-border);
  border-radius: 4px;
}

.form__submit {
  width: 100%;
  font-family: "Roboto Cond", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 16px;
  padding: 18px 0;
}

.form__text {
  font-size: var(--form-text-size);
  line-height: var(--form-line-height);
  text-indent: 2rem;
  margin-bottom: 14px;
}

.form__label {
  display: flex;
  flex-direction: column;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 16px;
  color: var(--main-color-secondary-);
}

.success {
  position: absolute;
  top: 50%;
  left: 50%;
  padding: 32px;
  font-family: "Roboto Cond", sans-serif;
  font-weight: 700;
  font-size: xx-large;
  color: #11c11c;
  background-color: var(--bg-color-basic);
  outline: 1px solid #11c11c;
  transform: translate(-50%, -50%);
}

.error {
  margin-top: 10px;
  padding: 5px;
  color: #c11111;
  background-color: var(--bg-color-basic);
  outline: 1px solid #c11111;
}

.page {
  height: 100%;
}
.page--column {
  display: flex;
  flex-direction: column;
}
.page--center {
  width: calc(100% - 30px);
  max-width: 400px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .page--center {
    width: calc(100% - 40px);
    max-width: 750px;
    margin: 0 auto;
  }
}
@media (min-width: 1190px) {
  .page--center {
    width: calc(100% - 50px);
    max-width: 1490px;
    margin: 0 auto;
  }
}
@media (min-width: 1550px) {
  .page--center {
    width: 100%;
    max-width: 1490px;
    margin: 0 auto;
  }
}

.page__body {
  min-width: 280px;
  min-height: 100%;
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  color: var(--font-color-basic);
  background-color: var(--bg-color-basic);
}

.page__title {
  max-width: 290px;
  margin: auto;
  margin-bottom: 20px;
  font-family: "Roboto Cond", sans-serif;
  font-weight: 700;
  font-size: 22px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 1190px) {
  .page__title {
    max-width: 538px;
    margin: 0;
    margin-bottom: 45px;
    font-size: 40px;
    line-height: 48px;
    text-align: start;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }
}

.page__header {
  flex-direction: row;
  min-width: var(--min-width);
  padding: 20px 0;
}

.page__content {
  position: relative;
  flex-grow: 1;
}

.page__footer {
  min-height: 198px;
  min-width: var(--min-width);
  padding: 33px 0 25px 0;
  color: var(--footer-color-seconadary);
  background-color: var(--footer-bg-color);
}

.header {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  padding: 15px 0 17px 0;
}
@media (min-width: 1190px) {
  .header {
    display: grid;
    grid-template-columns: 210px 360px 1fr 175px;
    grid-template-rows: 45px min-content;
    padding: 10px 0 21px 0;
  }
}
.header--fix {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  max-width: none;
  background-color: var(--bg-color-basic);
  box-shadow: 0px 5px 15px rgba(79, 64, 0, 0.15), inset 0px -1px 0px rgba(23, 22, 22, 0.2);
  z-index: 5;
}

.header__title {
  font-weight: 700;
  font-size: larger;
  text-align: center;
}

.header__text {
  font-size: 16px;
  line-height: 14px;
  color: var(--header-color-secondary);
}

.header__logo {
  width: 98px;
}
@media (min-width: 1190px) {
  .header__logo {
    grid-column: 1/2;
    grid-row: 1/-1;
    width: 150px;
  }
}

.header__contacts {
  display: none;
  font-style: normal;
}
@media (min-width: 1190px) {
  .header__contacts {
    grid-column: 3/4;
    grid-row: 1/2;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
  }
  .header__contacts::before {
    width: 18px;
    height: 23px;
    margin: 0 8px 0 1px;
    background-image: url("../img/icon_location.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    content: "";
  }
}

.header__calc {
  order: 2;
  position: relative;
  top: -1px;
  display: block;
  width: 72px;
  height: 38px;
  margin-left: auto;
  margin-right: 10px;
  background-image: url("../img/icon_calc.svg");
  background-size: 20px;
  background-position: center;
  background-repeat: no-repeat;
}
.header__calc--opened {
  background-image: url("../img/cross-close.svg");
}
@media (min-width: 425px) {
  .header__calc {
    margin-left: auto;
  }
}
@media (min-width: 1190px) {
  .header__calc--hide {
    display: none;
  }
}

.header__phone {
  order: 1;
  display: flex;
  flex-direction: column;
  margin-left: auto;
}
.header__phone-link {
  font-size: 16px;
  line-height: 14px;
  margin-bottom: 10px;
  color: inherit;
  font-family: "Roboto Cond", sans-serif;
  font-weight: 700;
  text-decoration: underline;
}
.header__phone-button {
  display: none;
  padding: 12px 0;
  margin: 12px 0 0 0;
  font-family: "Roboto Cond", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 14px;
  text-align: center;
  text-decoration: none;
}
.header__phone-button:hover {
  background-color: #007CC1;
}
@media (min-width: 380px) {
  .header__phone {
    flex-shrink: 0;
    order: 1;
  }
}
@media (min-width: 768px) {
  .header__phone {
    order: 1;
    margin: 0;
    margin-left: auto;
  }
}
@media (min-width: 1190px) {
  .header__phone {
    grid-column: 4/5;
    grid-row: 1/-1;
    display: flex;
    width: auto;
    height: auto;
  }
  .header__phone-link {
    margin: 0;
    font-size: 22px;
    line-height: 26px;
    letter-spacing: -0.3px;
  }
  .header__phone-button {
    display: block;
  }
}

.header__phone-text {
  display: none;
}
@media (min-width: 1190px) {
  .header__phone-text {
    display: block;
    width: 100%;
    color: var(--font-color-inv);
  }
}

.logo__image {
  width: 100%;
  margin-top: 4px;
  height: auto;
  object-fit: contain;
}

.footer {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1190px) {
  .footer {
    display: grid;
    grid-template-columns: 1fr;
    padding: 0 10px;
  }
}
@media (min-width: 1550px) {
  .footer {
    padding: 0;
  }
}

.footer__section {
  display: flex;
  flex-direction: column;
}

.footer__links {
  align-items: center;
  max-width: 300px;
}
@media (min-width: 1190px) {
  .footer__links {
    display: grid;
    grid-template-columns: 1fr 200px 180px 180px 1fr;
    grid-template-rows: auto min-content;
    column-gap: 20px;
    width: 100%;
    max-width: 1490px;
  }
}

.footer__menu {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-bottom: 14px;
  font-family: "Roboto Cond", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
}
@media (min-width: 1190px) {
  .footer__menu {
    flex-wrap: nowrap;
    justify-content: flex-start;
    grid-column: 2/5;
    grid-row: 1/2;
  }
}

.footer__menu-link {
  width: 50%;
  margin-bottom: 10px;
}
.footer__menu-link:nth-of-type(2n) {
  text-align: end;
}
@media (min-width: 1190px) {
  .footer__menu-link {
    position: relative;
    width: auto;
    font-size: 20px;
    line-height: 23px;
    padding-right: 25px;
  }
  .footer__menu-link:nth-of-type(2n) {
    text-align: initial;
  }
  .footer__menu-link:hover {
    color: #919191;
  }
  .footer__menu-link:not(:last-of-type)::after {
    display: block;
    content: "|";
    position: absolute;
    right: 10px;
    top: -1px;
    font-family: "RobotoCondensed", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 23px;
    text-align: center;
    color: #032233;
    text-decoration: none;
  }
}

.footer__logos {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 0;
  margin-bottom: 30px;
}
@media (min-width: 1190px) {
  .footer__logos {
    grid-column: 1/2;
    grid-row: 1/-1;
    justify-content: flex-start;
  }
}

.footer__logo {
  width: 145px;
}
@media (min-width: 1190px) {
  .footer__logo {
    width: 188px;
  }
}

.footer__star {
  width: 65px;
}
@media (min-width: 1190px) {
  .footer__star {
    width: 94px;
    height: auto;
  }
}

.footer__text {
  margin-bottom: 15px;
  font-size: 16px;
  line-height: 140%;
  text-align: center;
  color: #032233;
}

.footer__socials {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin: 0 0 15px 0;
}
@media (min-width: 1190px) {
  .footer__socials {
    justify-content: space-evenly;
    grid-column: 2/3;
    grid-row: 2/3;
  }
}

.footer__socials-item {
  display: block;
  width: 26px;
  height: 26px;
  margin-bottom: 5px;
  background-color: var(--bg-color-secondary);
  -webkit-mask-size: cover;
  mask-size: cover;
  -webkit-mask-repeat: no-repeat;
  mask-repeat: no-repeat;
}
.footer__socials-item:not(:last-of-type) {
  margin-right: 15px;
}
.footer__socials-item:hover {
  background-color: #032233;
}

@media (min-width: 1190px) {
  .yandex-badge {
    grid-column: 3/4;
    grid-row: 2/3;
  }
}

@media (min-width: 1190px) {
  .footer__yell-badge {
    grid-column: 4/5;
    grid-row: 2/3;
  }
}

.footer__phone {
  -webkit-mask-image: url("../img/phone-call_0_1.svg");
  mask-image: url("../img/phone-call_0_1.svg");
}

.footer__email {
  -webkit-mask-image: url("../img/mail_ic.svg");
  mask-image: url("../img/mail_ic.svg");
}

.footer__claim {
  margin: 20px 0;
  font-size: 16px;
  line-height: 16px;
  text-align: center;
}
@media (min-width: 1190px) {
  .footer__claim {
    grid-column: 5/6;
    grid-row: 1/-1;
    max-width: 215px;
    margin: 0;
    margin-left: auto;
    font-size: 16px;
    line-height: 172%;
  }
}

.footer__info {
  padding: 15px 0 0 0;
}
@media (min-width: 1190px) {
  .footer__info {
    flex-direction: row-reverse;
    justify-content: space-between;
    margin-top: 25px;
    padding: 0;
  }
}

.socials {
  display: flex;
  flex-wrap: wrap;
}

.socials__vk {
  -webkit-mask-image: url("../img/vk_ic.svg");
  mask-image: url("../img/vk_ic.svg");
}

.socials__telegram {
  -webkit-mask-image: url("../img/telegram_ic.svg");
  mask-image: url("../img/telegram_ic.svg");
}

.socials__youtube {
  -webkit-mask-image: url("../img/youtube_ic.svg");
  mask-image: url("../img/youtube_ic.svg");
}

.section {
  display: flex;
  flex-direction: column;
}
.section--center {
  flex-grow: 1;
  align-items: center;
  justify-content: stretch;
  font-family: "Roboto Cond", sans-serif;
  font-weight: 700;
  font-size: 48px;
  line-height: 64px;
  text-align: center;
  color: var(--font-color-secondary);
}
@media (min-width: 1190px) {
  .section--center {
    margin-top: 50px;
    font-size: 72px;
    line-height: 84px;
  }
}

.section__title {
  margin-bottom: 30px;
  font-family: "Roboto Cond", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 1190px) {
  .section__title {
    font-size: 40px;
    line-height: 48px;
  }
}
.section__title--inherit {
  font: inherit;
  color: inherit;
}

.section__subtitle {
  padding: 10px 10px;
  font-family: "Roboto Cond", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 1190px) {
  .section__subtitle {
    font-size: 30px;
    line-height: 36px;
  }
}

.section__text {
  font-style: normal;
  font-size: 16px;
  line-height: 16px;
  text-indent: 2rem;
}
.section__text--inherit {
  font: inherit;
  color: inherit;
}

.section__wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 686px;
}

.accordion {
  position: relative;
}
.accordion:focus-within > .accordion__toggler::after {
  transform: rotate(180deg);
}

.accordion__toggler {
  position: relative;
  flex-shrink: 0;
}
.accordion__toggler::after {
  position: relative;
  top: -2px;
  display: block;
  width: 19px;
  height: 11px;
  margin-left: 7px;
  background-image: url("../img/icon_arrow_black_down.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transition: rotate 0.6s ease-out;
  content: "";
}
.accordion__toggler:focus ~ .accordion__item {
  display: flex;
  flex-direction: column;
  height: fit-content;
  background-color: transparent;
}

.accordion__item {
  display: none;
}

.carousel {
  display: flex;
  flex-direction: column;
}

.carousel__control {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  transform: translateY(-50%);
}
.carousel__control--dots {
  display: none;
  position: static;
  justify-content: center;
}

.carousel__dot {
  width: 12px;
  height: 12px;
  margin: 5px;
  background-color: var(--font-color-additional);
}
.carousel__dot--current {
  width: 46px;
  background-color: var(--btn-bg-basic);
}

.carousel__next {
  transform: rotate(180deg);
}

.carousel__btn {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-image: url("../img/icon_arrow_black_left.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 12px;
}
@media (min-width: 1190px) {
  .carousel__btn {
    width: 42px;
    height: 42px;
    background-size: 20px;
  }
}
.carousel__btn--white {
  background-image: url("../img/icon_arrow_white_left.svg");
}

.carousel__body {
  flex-direction: row;
  flex-wrap: nowrap;
  width: 100%;
  height: auto;
}

.carousel__slide {
  display: none;
  flex-shrink: 0;
  width: 100%;
  margin-bottom: auto;
}
.carousel__slide--mobile {
  display: flex;
}
@media (min-width: 1190px) {
  .carousel__slide {
    z-index: 1;
  }
  .carousel__slide--desktop {
    display: flex;
    flex-direction: column;
    width: min-content;
  }
}

